.userCard {
  position: relative;
  box-sizing: border-box;
  padding: 28px 20px 36px 20px;
  width: 380px;
  height: 460px;
  border-radius: 20px;
  background-color: #5736a3;
  background: linear-gradient(
    114.99deg,
    #471ca9 -0.99%,
    #5736a3 54.28%,
    #4b2a99 78.99%
  );
  box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.userCard::before {
  content: '';
  display: block;
  height: 8px;
  width: 100%;
  position: absolute;
  top: 214px;
  left: 0;
  background-color: #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
}

.logo {
  position: absolute;
  left: 20px;
  top: 20px;
}

.imageContainer {
  background-image: url(../../images/picture.png);
  background-repeat: no-repeat;
  background-position: center;
  height: 168px;
  width: 308px;
}

.avatarContainer {
  background-color: #5736a3;
  width: 64px;
  height: 64px;
  border: 8px solid #ebd8ff;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.06),
    inset 0px -1.71846px 3.43693px #ae7be3, inset 0px 3.43693px 2.5777px #fbf8ff;
  border-radius: 50%;
  position: absolute;
  top: 178px;
  left: 150px;
}

.avatar {
  border-radius: 50%;
}

.textContainer {
  text-align: center;
}

.text {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  text-transform: uppercase;
  margin-bottom: 16px;
  color: #ebd8ff;
}

.button {
  margin-top: 10px;
  width: 196px;
  padding: 14px;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;
  text-shadow: none;
  transition: all 250ms cubic-bezier(0.4, 0, 0.2, 1);

  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  border: currentColor;
}

.button:hover {
  cursor: pointer;
  transform: scale(1.05);
}

.following {
  background-color: #5cd3a8;
}

.follow {
  background-color: #ebd8ff;
}
